.home {
    position: relative;
}
.home:after {
    content : "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    /*background-image: url(../images/bg-home.jpg); */
    width: 100%;
    height: 100%;
    opacity : 0.1;
    z-index: -1;
}

.home-card {
